import React from "react";
import BitCard from "app/views/common/components/BitCard";
import Card from "react-bootstrap/Card";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Badge from "react-bootstrap/Badge";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBook,
  faBookOpen,
  faSitemap,
  faGlobe,
  faChalkboardTeacher,
  faSave,
} from "@fortawesome/free-solid-svg-icons";
import DeleteConfirmation from "app/views/common/components/DeleteConfirmation";
import { Redirect } from "react-router-dom";
import NoDataFound from "app/views/common/components/NoDataFound";

import { Editor } from "@tinymce/tinymce-react";
import { uploadImageFromBlobInfo } from "app/utils";
import TagsInput from "react-tagsinput";

import "./bit-details.scss";

function BitDetailsComponent(props) {
  const {
    activeBit: { deletedSuccess },
    editModeEnabled,
    toggleEditMode,
    localBitData,
    handleChange,
    isModified,
    saveBitDetails,
    deleteModeEnabled,
    toggleDeleteMode,
    deleteBit,
    prevActiveBitDeletedSuccessRef,
    tinyMceApiKey,
    handleEditorChange,
    handleTagsChange,
  } = props;

  if (!localBitData || localBitData.length === 0)
    return <NoDataFound message="This bit doesn't exist!" />;
  else if (!prevActiveBitDeletedSuccessRef.current && deletedSuccess)
    return <Redirect push to={{ pathname: `/courses` }} />;
  else {
    const { id, name, content, slug, url, author, tags = [] } = localBitData;

    return (
      <div id="cfg_bit_details_page" className="page p-0" key={id}>
        <DeleteConfirmation
          visible={deleteModeEnabled}
          onCancel={toggleDeleteMode}
          onOk={deleteBit}
          message={`Are you sure you want to delete this bit?`}
        />
        <Form onSubmit={saveBitDetails}>
          <div className="bit-header">
            <BitCard
              mode="header"
              {...localBitData}
              toggleEditMode={toggleEditMode}
              toggleDeleteMode={toggleDeleteMode}
            />
          </div>
          <div className="bit-info p-3">
            <Card className="border-0">
              <Card.Body>
                <div className="text-muted">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon icon={faBook} className="mr-2" />
                      Title
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group controlId="name" className="mb-0 mt-2">
                        <Form.Control
                          type="text"
                          value={name || ""}
                          placeholder="Enter title"
                          onChange={(e) => handleChange("name", e)}
                          required
                        />
                      </Form.Group>
                    ) : (
                      <>{name || "Not specified"}</>
                    )}
                  </div>
                </div>
                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon icon={faBookOpen} className="mr-2" />
                      Content
                    </strong>

                    {editModeEnabled ? (
                      <Form.Group controlId="content" className="mb-0 mt-2">
                        <Editor
                          apiKey={tinyMceApiKey}
                          key={"form-wvgiwys"}
                          initialValue={content || ""}
                          init={{
                            selector: "textarea",
                            images_upload_handler: function (
                              blobInfo,
                              success,
                              failure
                            ) {
                              uploadImageFromBlobInfo(
                                blobInfo,
                                success,
                                failure
                              );
                            },
                            height: 500,
                            menubar: false,
                            plugins: [
                              "advlist codesample autolink lists link image imagetools charmap print preview anchor",
                              "searchreplace visualblocks code fullscreen",
                              "insertdatetime media table paste code help wordcount",
                            ],
                            toolbar: `undo redo | formatselect | image | codesample | bold italic backcolor | 
             alignleft aligncenter alignright alignjustify | 
             bullist numlist outdent indent | removeformat | help`,
                            entity_encoding: "raw",
                          }}
                          onEditorChange={(content) =>
                            handleEditorChange("content", content)
                          }
                        />
                      </Form.Group>
                    ) : (
                      <>{content || "Not specified"}</>
                    )}
                  </div>
                </div>
                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon icon={faSitemap} className="mr-2" />
                      Slug
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group controlId="slug" className="mb-0 mt-2">
                        <Form.Control
                          type="text"
                          value={slug || ""}
                          placeholder="Enter slug"
                          onChange={(e) => handleChange("slug", e)}
                          required
                        />
                      </Form.Group>
                    ) : (
                      <>{slug || "Not specified"}</>
                    )}
                  </div>
                </div>
                {!editModeEnabled ? (
                  <div className="text-muted pt-3">
                    <div className="border-bottom mb-0 pb-3">
                      <strong className="d-block text-gray-dark">
                        <FontAwesomeIcon icon={faGlobe} className="mr-2" />
                        URL
                      </strong>
                      {url || "Not specified"}
                    </div>
                  </div>
                ) : null}

                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon
                        icon={faChalkboardTeacher}
                        className="mr-2"
                      />
                      Author
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group controlId="author" className="mb-0 mt-2">
                        <Form.Control
                          type="text"
                          value={author || ""}
                          placeholder="Enter author's name"
                          onChange={(e) => handleChange("author", e)}
                        />
                      </Form.Group>
                    ) : (
                      <>{author || "Not specified"}</>
                    )}
                  </div>
                </div>

                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon
                        icon={faChalkboardTeacher}
                        className="mr-2"
                      />
                      Tags
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group controlId="tags" className="mb-0 mt-2">
                        <TagsInput
                          value={tags === "" ? [] : tags}
                          onChange={(tags) => handleTagsChange("tags", tags)}
                        />
                      </Form.Group>
                    ) : (
                      <div className="bit-tags">
                        {tags && tags.length !== 0 ? (
                          <div className="tags">
                            {tags.map((tag, index) => (
                              <Badge
                                pill
                                variant="primary"
                                className="mr-1"
                                key={`tag-${index}`}
                              >
                                {tag}
                              </Badge>
                            ))}
                          </div>
                        ) : (
                          "Not specified"
                        )}
                      </div>
                    )}
                  </div>
                </div>
              </Card.Body>
            </Card>
          </div>
          <div className="bit-action-panel">
            <div className="p-3">
              <Button
                type="submit"
                variant="success"
                className="ml-2"
                disabled={!isModified}
              >
                <FontAwesomeIcon icon={faSave} className="mr-2" />
                Save
              </Button>
            </div>
          </div>
        </Form>
      </div>
    );
  }
}

export default BitDetailsComponent;
