import produce from "immer";
import * as types from "./types";

const initialState = {
  isLoading: false,
  error: false,
  errMsg: undefined,
  data: [],
};

const appReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case types.LOADING_BITS: {
        draft.isLoading = true;
        draft.error = false;
        draft.errMsg = undefined;
        break;
      }
      case types.SET_BITS: {
        const { payload } = action;
        draft.isLoading = false;
        draft.data = payload;
        draft.error = false;
        draft.errMsg = undefined;
        break;
      }
      case types.LOAD_BITS_ERROR: {
        const { payload } = action;
        draft.isLoading = false;
        draft.error = true;
        draft.errMsg = payload;
        break;
      }
      default:
        break;
    }
  });

export default appReducer;
