import * as types from "./types";

// Loading Bit Data Actions
export const loadBit = (payload) => ({
  type: types.LOAD_BIT,
  payload,
});

export const loadingBit = () => ({
  type: types.LOADING_BIT,
});

export const setBit = (payload) => ({
  type: types.SET_BIT,
  payload,
});

export const loadBitError = (payload) => ({
  type: types.LOAD_BIT_ERROR,
  payload,
});

// Saving Bit Details Actions
export const saveNewBitDetails = (data) => ({
  type: types.SAVE_BIT_DETAILS,
  payload: {
    data,
    mode: "add",
  },
});

export const saveBitDetails = (data) => ({
  type: types.SAVE_BIT_DETAILS,
  payload: {
    data,
    mode: "update",
  },
});

export const savingBitDetails = () => ({
  type: types.SAVING_BIT_DETAILS,
});

export const savedBitDetails = (payload) => ({
  type: types.SAVED_BIT_DETAILS,
  payload,
});

export const saveBitDetailsError = (payload) => ({
  type: types.SAVE_BIT_DETAILS_ERROR,
  payload,
});

export const resetSaveBitDetailsFlag = () => ({
  type: types.RESET_SAVE_BIT_DETAILS_FLAG,
});

// Deleting Bit Details Actions
export const deleteBitDetails = (payload) => ({
  type: types.DELETE_BIT_DETAILS,
  payload,
});

export const deletingBitDetails = () => ({
  type: types.DELETING_BIT_DETAILS,
});

export const deletedBitDetails = (payload) => ({
  type: types.DELETED_BIT_DETAILS,
  payload,
});

export const deleteBitDetailsError = (payload) => ({
  type: types.DELETE_BIT_DETAILS_ERROR,
  payload,
});

export const resetDeleteBitDetailsFlag = () => ({
  type: types.RESET_DELETE_BIT_DETAILS_FLAG,
});

// Publish Bit Details Actions
export const publishBitDetails = (id, isPublished) => ({
  type: types.PUBLISH_BIT_DETAILS,
  payload: {
    id,
    isPublished,
  },
});

export const publishingBitDetails = () => ({
  type: types.PUBLISHING_BIT_DETAILS,
});

export const publishedBitDetails = (payload) => ({
  type: types.PUBLISHED_BIT_DETAILS,
  payload,
});

export const publishBitDetailsError = (payload) => ({
  type: types.PUBLISH_BIT_DETAILS_ERROR,
  payload,
});

export const resetPublishBitDetailsFlag = () => ({
  type: types.RESET_PUBLISH_BIT_DETAILS_FLAG,
});
