import * as types from "./types";
import * as bitActions from "./actions";
import { createLogic } from "redux-logic";
import get from "lodash.get";
import axios from "axios";
import env from "app/environment";
import {
  SAVE_DETAILS_FLAG_RESET_DELAY,
  DELETE_DETAILS_FLAG_RESET_DELAY,
} from "app/constants/delay";

const loadBitLogic = createLogic({
  type: types.LOAD_BIT,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(bitActions.loadingBit());
    const bitId = action.payload;
    if (bitId) {
      axios
        .get(env.BIT(bitId))
        .then((res) => {
          if (res.data.error) {
            dispatch(
              bitActions.loadBitError(
                res.data.message || "ERR: An error occured"
              )
            );
          } else {
            dispatch(bitActions.setBit(get(res, `data.data.bits`, {})));
          }
        })
        .catch((err) => {
          dispatch(bitActions.loadBitError("Server error"));
        })
        .finally(() => {
          done();
        });
    } else {
      done();
    }
  },
});

const saveBitDetailsLogic = createLogic({
  type: types.SAVE_BIT_DETAILS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(bitActions.savingBitDetails());
    const { data: bitDetails, mode } = action.payload;

    if (mode === "update") {
      const activeBitId = get(getState(), `activeBit.data.id`, undefined);
      if (activeBitId) {
        axios
          .put(env.BIT(activeBitId), bitDetails)
          .then((res) => {
            if (res.data.error) {
              const message = get(
                res,
                `data.message`,
                "An error occured while updating"
              );
              dispatch(bitActions.saveBitDetailsError(message));
              setTimeout(() => {
                dispatch(bitActions.resetSaveBitDetailsFlag());
                done();
              }, SAVE_DETAILS_FLAG_RESET_DELAY);
            } else {
              const message = get(
                res,
                `data.data.message`,
                "Updated successfully"
              );

              dispatch(bitActions.savedBitDetails(message));
              dispatch(bitActions.loadBit(activeBitId));

              setTimeout(() => {
                dispatch(bitActions.resetSaveBitDetailsFlag());
                done();
              }, SAVE_DETAILS_FLAG_RESET_DELAY);
            }
          })
          .catch((err) => {
            dispatch(bitActions.saveBitDetailsError("Server error"));
            setTimeout(() => {
              dispatch(bitActions.resetSaveBitDetailsFlag());
              done();
            }, SAVE_DETAILS_FLAG_RESET_DELAY);
          });
      } else {
        console.log("Active bit Id not found");
        done();
      }
    } else if (mode === "add") {
      axios
        .post(env.BITS, bitDetails)
        .then((res) => {
          if (res.data.error) {
            const message = get(
              res,
              `data.message`,
              "An error occured while updating"
            );
            dispatch(bitActions.saveBitDetailsError(message));
            setTimeout(() => {
              dispatch(bitActions.resetSaveBitDetailsFlag());
              done();
            }, SAVE_DETAILS_FLAG_RESET_DELAY);
          } else {
            const message = get(
              res,
              `data.data.message`,
              "Updated successfully"
            );

            dispatch(bitActions.savedBitDetails(message));

            setTimeout(() => {
              dispatch(bitActions.resetSaveBitDetailsFlag());
              done();
            }, SAVE_DETAILS_FLAG_RESET_DELAY);
          }
        })
        .catch((err) => {
          dispatch(bitActions.saveBitDetailsError("Server error"));
          setTimeout(() => {
            dispatch(bitActions.resetSaveBitDetailsFlag());
            done();
          }, SAVE_DETAILS_FLAG_RESET_DELAY);
        });
    } else {
      done();
    }
  },
});

const deleteBitDetailsLogic = createLogic({
  type: types.DELETE_BIT_DETAILS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(bitActions.deletingBitDetails());
    const activeBitId = get(getState(), `activeBit.data.id`, undefined);

    if (activeBitId) {
      axios
        .delete(env.BIT(activeBitId))
        .then((res) => {
          if (res.data.error) {
            const message = get(
              res,
              `data.message`,
              "An error occured while deleting"
            );
            dispatch(bitActions.deleteBitDetailsError(message));
            setTimeout(() => {
              dispatch(bitActions.resetDeleteBitDetailsFlag());
              done();
            }, DELETE_DETAILS_FLAG_RESET_DELAY);
          } else {
            const message = get(
              res,
              `data.data.message`,
              "Deleted successfully"
            );

            dispatch(bitActions.deletedBitDetails(message));
            // dispatch(bitActions.loadBit(activeBitId));

            setTimeout(() => {
              dispatch(bitActions.resetDeleteBitDetailsFlag());
              done();
            }, DELETE_DETAILS_FLAG_RESET_DELAY);
          }
        })
        .catch((err) => {
          dispatch(bitActions.deleteBitDetailsError("Server error"));
          setTimeout(() => {
            dispatch(bitActions.resetDeleteBitDetailsFlag());
            done();
          }, DELETE_DETAILS_FLAG_RESET_DELAY);
        });
    } else {
      done();
    }
  },
});

export default [loadBitLogic, saveBitDetailsLogic, deleteBitDetailsLogic];
