import * as types from "./types";
import * as appActions from "./actions";
import { createLogic } from "redux-logic";
import get from "lodash.get";
import axios from "axios";
import env from "app/environment";
import { setAuthToken } from "app/utils";

const loginLogic = createLogic({
  type: types.LOGIN,
  latest: true,
  process({ getState, action }, dispatch, done) {
    const { userName, password } = action.payload;
    if (userName && password) {
      axios
        .post(env.LOGIN, { userName, password })
        .then((res) => {
          if (res.data.error) {
            dispatch(
              appActions.loginFailed(
                res.data.message || "ERR: An error occured"
              )
            );
          } else {
            const token = get(res, `data.token`, undefined);
            const tinyMceApiKey = get(res, `data.tinyKey`, undefined);

            setAuthToken(token);
            dispatch(
              appActions.loginSuccess({ token, userName, tinyMceApiKey })
            );
          }
        })
        .catch((err) => {
          dispatch(appActions.loginFailed("Server error"));
        })
        .finally(() => {
          done();
        });
    } else {
      done();
    }
  },
});

const logoutLogic = createLogic({
  type: types.LOGOUT,
  latest: true,
  process({ getState, action }, dispatch, done) {
    setAuthToken();
    done();
  },
});

export default [loginLogic, logoutLogic];
