import * as types from "./types";

// Loading Lesson Data Actions
export const login = (payload) => ({
  type: types.LOGIN,
  payload,
});

export const loginSuccess = (payload) => ({
  type: types.LOGIN_SUCCESS,
  payload,
});

export const loginFailed = (payload) => ({
  type: types.LOGIN_FAILED,
  payload,
});

export const logout = () => ({
  type: types.LOGOUT,
});
