import React from "react";
import { Provider } from "react-redux";
import configureStore from "app/store";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import PrivateRoute from "app/views/common/containers/PrivateRoute";
import SCREENS from "app/views/pages";
import Indicator from "app/views/common/containers/Indicator";
import "./app.scss";

const store = configureStore();

function App() {
  return (
    <div className="app" id="cfg_course_gen_ui">
      <Provider store={store}>
        <Indicator />
        <BrowserRouter>
          <Switch>
            <PrivateRoute
              path="/courses"
              exact={true}
              component={SCREENS.COURSES}
            />
            <PrivateRoute
              path="/add-course"
              exact={true}
              component={SCREENS.ADD_COURSE}
            />
            <PrivateRoute
              path="/add-bit"
              exact={true}
              component={SCREENS.ADD_BIT}
            />
            <PrivateRoute
              path="/course/:id"
              exact={true}
              component={SCREENS.COURSE_DETAILS}
            />
            <PrivateRoute
              path="/bit/:id"
              exact={true}
              component={SCREENS.BIT_DETAILS}
            />
            <PrivateRoute
              path="/course/:id/lessons"
              exact={true}
              component={SCREENS.LESSONS}
            />
            <PrivateRoute
              path="/course/:id/modules"
              exact={true}
              component={SCREENS.MODULES}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/`}
              exact={true}
              component={SCREENS.LOGIN}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/login`}
              exact={true}
              component={SCREENS.LOGIN}
            />
            <Route component={SCREENS.NOT_FOUND} />
          </Switch>
        </BrowserRouter>
      </Provider>
    </div>
  );
}

export default App;
