import React from "react";
import { useSelector } from "react-redux";
import Modal from "react-bootstrap/Modal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faSpinner,
  faCheckCircle,
  faTimesCircle,
} from "@fortawesome/free-solid-svg-icons";

export default function Indicator() {
  const activeCourse = useSelector((state) => {
    const { data, ...rest } = state.activeCourse;
    return rest;
  });

  const activeBit = useSelector((state) => {
    const { data, ...rest } = state.activeBit;
    return rest;
  });

  const activeCourseLessons = useSelector((state) => {
    const { data, ...rest } = state.activeCourseLessons;
    return rest;
  });

  const {
    //Active course loading transients
    isLoading: activeCourseLoading,
    error: activeCourseError,
    errMsg: activeCourseErrMsg,

    //Active course saving transients
    isSaving: activeCourseSaving,
    savedSuccess: activeCourseSavedSuccess,
    saveError: activeCourseSaveError,
    saveMsg: activeCourseSaveMsg,

    //Active course deleting transients
    isDeleting: activeCourseDeleting,
    deletedSuccess: activeCourseDeletedSuccess,
    deleteError: activeCourseDeleteError,
    deleteMsg: activeCourseDeleteMsg,
  } = activeCourse;

  const {
    //Active course lessons loading transients
    isLoading: activeCourseLessonsLoading,
    error: activeCourseLessonsError,
    errMsg: activeCourseLessonsErrMsg,

    //Active course lesson saving transients
    isSaving: activeCourseLessonSaving,
    savedSuccess: activeCourseLessonSavedSuccess,
    saveError: activeCourseLessonSaveError,
    saveMsg: activeCourseLessonSaveMsg,

    //Active course lesson deleting transients
    isDeleting: activeCourseLessonDeleting,
    deletedSuccess: activeCourseLessonDeletedSuccess,
    deleteError: activeCourseLessonDeleteError,
    deleteMsg: activeCourseLessonDeleteMsg,

    // Active course lessons sequence transients
    isSequenceSaving: activeCourseLessonsSequenceSaving,
    sequenceSaveError: activeCourseLessonsSequenceSaveError,
    sequenceSaveSuccess: activeCourseLessonsSequenceSaveSuccess,
    sequenceSaveMsg: activeCourseLessonsSequenceSaveMsg,
  } = activeCourseLessons;

  const {
    //Active course loading transients
    isLoading: activeBitLoading,
    error: activeBitError,
    errMsg: activeBitErrMsg,

    //Active course saving transients
    isSaving: activeBitSaving,
    savedSuccess: activeBitSavedSuccess,
    saveError: activeBitSaveError,
    saveMsg: activeBitSaveMsg,

    //Active course deleting transients
    isDeleting: activeBitDeleting,
    deletedSuccess: activeBitDeletedSuccess,
    deleteError: activeBitDeleteError,
    deleteMsg: activeBitDeleteMsg,
  } = activeBit;

  return (
    <div id="cfg_indicator">
      <Modal
        show={
          activeCourseLoading ||
          activeCourseError ||
          activeCourseSaving ||
          activeCourseSavedSuccess ||
          activeCourseSaveError ||
          activeCourseDeleting ||
          activeCourseDeletedSuccess ||
          activeCourseDeleteError ||
          activeCourseLessonsLoading ||
          activeCourseLessonsError ||
          activeCourseLessonSaving ||
          activeCourseLessonSavedSuccess ||
          activeCourseLessonSaveError ||
          activeCourseLessonDeleting ||
          activeCourseLessonDeletedSuccess ||
          activeCourseLessonDeleteError ||
          activeCourseLessonsSequenceSaving ||
          activeCourseLessonsSequenceSaveError ||
          activeCourseLessonsSequenceSaveSuccess ||
          activeCourseLessonsSequenceSaveMsg ||
          activeBitLoading ||
          activeBitError ||
          activeBitSaving ||
          activeBitSavedSuccess ||
          activeBitSaveError ||
          activeBitDeleting ||
          activeBitDeletedSuccess ||
          activeBitDeleteError
        }
        centered
        size="lg"
      >
        <Modal.Body>
          {activeCourseLoading ? (
            //Active Course - Loading Transients
            <div className="p-3 align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faSpinner}
                className="mr-2"
                size="2x"
                spin
              />
              Loading course...
            </div>
          ) : activeCourseError ? (
            <div className="p-3 text-danger align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faTimesCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseErrMsg}
            </div>
          ) : activeCourseSaving ? (
            //Active Course - Saving Transients
            <div className="p-3 align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faSpinner}
                className="mr-2"
                size="2x"
                spin
              />
              Saving...
            </div>
          ) : activeCourseSavedSuccess ? (
            <div className="p-3 text-success align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faCheckCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseSaveMsg}
            </div>
          ) : activeCourseSaveError ? (
            <div className="p-3 text-danger align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faTimesCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseSaveMsg}
            </div>
          ) : activeCourseDeleting ? (
            //Active Course - Deleting Transients
            <div className="p-3 align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faSpinner}
                className="mr-2"
                size="2x"
                spin
              />
              Deleting course...
            </div>
          ) : activeCourseDeletedSuccess ? (
            <div className="p-3 text-success align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faCheckCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseDeleteMsg}
            </div>
          ) : activeCourseDeleteError ? (
            <div className="p-3 text-danger align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faTimesCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseDeleteMsg}
            </div>
          ) : activeCourseLessonsLoading ? (
            //Active Course Lessons - Loading Transients
            <div className="p-3 align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faSpinner}
                className="mr-2"
                size="2x"
                spin
              />
              Loading lessons...
            </div>
          ) : activeCourseLessonsError ? (
            <div className="p-3 text-danger align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faTimesCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseLessonsErrMsg}
            </div>
          ) : activeCourseLessonSaving ? (
            //Active Course Lesson - Saving Transients
            <div className="p-3 align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faSpinner}
                className="mr-2"
                size="2x"
                spin
              />
              Saving lesson...
            </div>
          ) : activeCourseLessonSavedSuccess ? (
            <div className="p-3 text-success align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faCheckCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseLessonSaveMsg}
            </div>
          ) : activeCourseLessonSaveError ? (
            <div className="p-3 text-danger align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faTimesCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseLessonSaveMsg}
            </div>
          ) : activeCourseLessonDeleting ? (
            //Active Course Lesson - Deleting Transients
            <div className="p-3 align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faSpinner}
                className="mr-2"
                size="2x"
                spin
              />
              Deleting lesson...
            </div>
          ) : activeCourseLessonDeletedSuccess ? (
            <div className="p-3 text-success align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faCheckCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseLessonDeleteMsg}
            </div>
          ) : activeCourseLessonDeleteError ? (
            <div className="p-3 text-danger align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faTimesCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseLessonDeleteMsg}
            </div>
          ) : activeCourseLessonsSequenceSaving ? (
            <div className="p-3 align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faSpinner}
                className="mr-2"
                size="2x"
                spin
              />
              Saving lesson sequence...
            </div>
          ) : activeCourseLessonsSequenceSaveError ? (
            <div className="p-3 text-danger align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faTimesCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseLessonsSequenceSaveMsg}
            </div>
          ) : activeCourseLessonsSequenceSaveSuccess ? (
            <div className="p-3 text-success align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faCheckCircle}
                className="mr-2"
                size="2x"
              />
              {activeCourseLessonsSequenceSaveMsg}
            </div>
          ) : activeBitLoading ? (
            //Active Course - Loading Transients
            <div className="p-3 align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faSpinner}
                className="mr-2"
                size="2x"
                spin
              />
              Loading bit...
            </div>
          ) : activeBitError ? (
            <div className="p-3 text-danger align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faTimesCircle}
                className="mr-2"
                size="2x"
              />
              {activeBitErrMsg}
            </div>
          ) : activeBitSaving ? (
            //Active Course - Saving Transients
            <div className="p-3 align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faSpinner}
                className="mr-2"
                size="2x"
                spin
              />
              Saving...
            </div>
          ) : activeBitSavedSuccess ? (
            <div className="p-3 text-success align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faCheckCircle}
                className="mr-2"
                size="2x"
              />
              {activeBitSaveMsg}
            </div>
          ) : activeBitSaveError ? (
            <div className="p-3 text-danger align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faTimesCircle}
                className="mr-2"
                size="2x"
              />
              {activeBitSaveMsg}
            </div>
          ) : activeBitDeleting ? (
            //Active Course - Deleting Transients
            <div className="p-3 align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faSpinner}
                className="mr-2"
                size="2x"
                spin
              />
              Deleting bit...
            </div>
          ) : activeBitDeletedSuccess ? (
            <div className="p-3 text-success align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faCheckCircle}
                className="mr-2"
                size="2x"
              />
              {activeBitDeleteMsg}
            </div>
          ) : activeBitDeleteError ? (
            <div className="p-3 text-danger align-items-center d-flex ">
              <FontAwesomeIcon
                icon={faTimesCircle}
                className="mr-2"
                size="2x"
              />
              {activeBitDeleteMsg}
            </div>
          ) : null}
        </Modal.Body>
      </Modal>
    </div>
  );
}
