import produce from "immer";
import * as types from "./types";

const initialState = {
  // Loading
  isLoading: false,
  error: false,
  errMsg: undefined,
  data: [],

  // Saving
  isSaving: false,
  savedSuccess: false,
  saveError: false,
  saveMsg: undefined,

  // Deleting
  isDeleting: false,
  deletedSuccess: false,
  deleteError: false,
  deleteMsg: undefined,
};

const appReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case types.LOADING_MODULES: {
        draft.isLoading = true;
        draft.error = false;
        draft.errMsg = undefined;
        break;
      }
      case types.SET_MODULES: {
        const { payload } = action;
        draft.isLoading = false;
        draft.data = payload;
        draft.error = false;
        draft.errMsg = undefined;
        break;
      }
      case types.LOAD_MODULES_ERROR: {
        const { payload } = action;
        draft.isLoading = false;
        draft.error = true;
        draft.errMsg = payload;
        break;
      }
      //Saving Course Details
      case types.SAVING_MODULE_DETAILS: {
        draft.isSaving = true;
        draft.saveError = false;
        draft.saveMsg = undefined;
        draft.savedSuccess = false;
        break;
      }
      case types.SAVED_MODULE_DETAILS: {
        const { payload } = action;
        draft.isSaving = false;
        draft.saveError = false;
        draft.saveMsg = payload;
        draft.savedSuccess = true;
        break;
      }
      case types.SAVE_MODULE_DETAILS_ERROR: {
        const { payload } = action;
        draft.isSaving = false;
        draft.saveError = true;
        draft.saveMsg = payload;
        draft.savedSuccess = false;
        break;
      }
      case types.RESET_SAVE_MODULE_DETAILS_FLAG: {
        if (state.savedSuccess) draft.savedSuccess = false;
        if (state.saveError) draft.saveError = false;
        draft.saveMsg = undefined;
        break;
      }
      //Deleting Course Details
      case types.DELETING_MODULE_DETAILS: {
        draft.isDeleting = true;
        draft.deleteError = false;
        draft.deleteMsg = undefined;
        draft.deletedSuccess = false;
        break;
      }
      case types.DELETED_MODULE_DETAILS: {
        const { payload } = action;
        draft.isDeleting = false;
        draft.deleteError = false;
        draft.deleteMsg = payload;
        draft.deletedSuccess = true;
        break;
      }
      case types.DELETE_MODULE_DETAILS_ERROR: {
        const { payload } = action;
        draft.isDeleting = false;
        draft.deleteError = true;
        draft.deleteMsg = payload;
        draft.deletedSuccess = false;
        break;
      }
      case types.RESET_DELETE_MODULE_DETAILS_FLAG: {
        if (state.deletedSuccess) draft.deletedSuccess = false;
        if (state.deleteError) draft.deleteError = false;
        draft.deleteMsg = undefined;
        break;
      }
      default:
        break;
    }
  });

export default appReducer;
