import * as types from "./types";

export const loadBits = () => ({
  type: types.LOAD_BITS,
});

export const loadingBits = () => ({
  type: types.LOADING_BITS,
});

export const setBits = (payload) => ({
  type: types.SET_BITS,
  payload,
});

export const loadBitsError = (payload) => ({
  type: types.LOAD_BITS_ERROR,
  payload,
});
