import produce from "immer";
import { LOGIN_SUCCESS, LOGOUT, LOGIN_FAILED } from "./types";

const initialState = {
  auth: {
    isAuthenticated: false,
    token: undefined,
    error: false,
    errMsg: undefined,
  },
  tinyMceApiKey: undefined,
  user: {
    userName: undefined,
  },
};

const appReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case LOGIN_SUCCESS: {
        const { userName, token, tinyMceApiKey } = action.payload;
        draft.auth.isAuthenticated = true;
        draft.auth.token = token;
        draft.user.userName = userName;
        draft.auth.error = false;
        draft.auth.errMsg = undefined;
        draft.tinyMceApiKey = tinyMceApiKey;
        break;
      }
      case LOGIN_FAILED: {
        draft.auth.isAuthenticated = false;
        draft.auth.token = undefined;
        draft.user.userName = undefined;
        draft.auth.error = true;
        draft.auth.errMsg = action.payload;
        draft.tinyMceApiKey = undefined;
        break;
      }
      case LOGOUT: {
        draft.auth.isAuthenticated = false;
        draft.auth.token = undefined;
        draft.user.userName = undefined;
        draft.auth.error = true;
        draft.auth.errMsg = action.payload;
        draft.tinyMceApiKey = undefined;
        break;
      }
      default:
        break;
    }
  });

export default appReducer;
