import * as types from "./types";

export const loadCourses = () => ({
  type: types.LOAD_COURSES,
});

export const loadingCourses = () => ({
  type: types.LOADING_COURSES,
});

export const setCourses = (payload) => ({
  type: types.SET_COURSES,
  payload,
});

export const loadCoursesError = (payload) => ({
  type: types.LOAD_COURSES_ERROR,
  payload,
});
