import * as types from "./types";

// Loading Course Data Actions
export const loadCourse = (payload) => ({
  type: types.LOAD_COURSE,
  payload,
});

export const loadingCourse = () => ({
  type: types.LOADING_COURSE,
});

export const setCourse = (payload) => ({
  type: types.SET_COURSE,
  payload,
});

export const loadCourseError = (payload) => ({
  type: types.LOAD_COURSE_ERROR,
  payload,
});

// Saving Course Details Actions
export const saveNewCourseDetails = (data) => ({
  type: types.SAVE_COURSE_DETAILS,
  payload: {
    data,
    mode: "add",
  },
});

export const saveCourseDetails = (data) => ({
  type: types.SAVE_COURSE_DETAILS,
  payload: {
    data,
    mode: "update",
  },
});

export const savingCourseDetails = () => ({
  type: types.SAVING_COURSE_DETAILS,
});

export const savedCourseDetails = (payload) => ({
  type: types.SAVED_COURSE_DETAILS,
  payload,
});

export const saveCourseDetailsError = (payload) => ({
  type: types.SAVE_COURSE_DETAILS_ERROR,
  payload,
});

export const resetSaveCourseDetailsFlag = () => ({
  type: types.RESET_SAVE_COURSE_DETAILS_FLAG,
});

// Deleting Course Details Actions
export const deleteCourseDetails = (payload) => ({
  type: types.DELETE_COURSE_DETAILS,
  payload,
});

export const deletingCourseDetails = () => ({
  type: types.DELETING_COURSE_DETAILS,
});

export const deletedCourseDetails = (payload) => ({
  type: types.DELETED_COURSE_DETAILS,
  payload,
});

export const deleteCourseDetailsError = (payload) => ({
  type: types.DELETE_COURSE_DETAILS_ERROR,
  payload,
});

export const resetDeleteCourseDetailsFlag = () => ({
  type: types.RESET_DELETE_COURSE_DETAILS_FLAG,
});

// Publish Course Details Actions
export const publishCourseDetails = (id, isPublished) => ({
  type: types.PUBLISH_COURSE_DETAILS,
  payload: {
    id,
    isPublished,
  },
});

export const publishingCourseDetails = () => ({
  type: types.PUBLISHING_COURSE_DETAILS,
});

export const publishedCourseDetails = (payload) => ({
  type: types.PUBLISHED_COURSE_DETAILS,
  payload,
});

export const publishCourseDetailsError = (payload) => ({
  type: types.PUBLISH_COURSE_DETAILS_ERROR,
  payload,
});

export const resetPublishCourseDetailsFlag = () => ({
  type: types.RESET_PUBLISH_COURSE_DETAILS_FLAG,
});
