import * as types from "./types";

// Loading Lesson Data Actions
export const loadLessons = (payload) => ({
  type: types.LOAD_LESSONS,
  payload,
});

export const loadingLessons = () => ({
  type: types.LOADING_LESSONS,
});

export const setLessons = (payload) => ({
  type: types.SET_LESSONS,
  payload,
});

export const loadLessonsError = (payload) => ({
  type: types.LOAD_LESSONS_ERROR,
  payload,
});

// Saving Lesson Details Actions
export const addNewLessonDetails = (courseId, lesson) => ({
  type: types.SAVE_LESSON_DETAILS,
  payload: {
    mode: "add",
    courseId,
    data: lesson,
  },
});

export const saveLessonDetails = (courseId, lesson) => ({
  type: types.SAVE_LESSON_DETAILS,
  payload: {
    mode: "update",
    courseId,
    data: lesson,
  },
});

export const savingLessonDetails = () => ({
  type: types.SAVING_LESSON_DETAILS,
});

export const savedLessonDetails = (payload) => ({
  type: types.SAVED_LESSON_DETAILS,
  payload,
});

export const saveLessonDetailsError = (payload) => ({
  type: types.SAVE_LESSON_DETAILS_ERROR,
  payload,
});

export const resetSaveLessonDetailsFlag = () => ({
  type: types.RESET_SAVE_LESSON_DETAILS_FLAG,
});

// Deleting Lesson Details Actions
export const deleteLessonDetails = (courseId, lessonId) => ({
  type: types.DELETE_LESSON_DETAILS,
  payload: {
    courseId,
    lessonId,
  },
});

export const deletingLessonDetails = () => ({
  type: types.DELETING_LESSON_DETAILS,
});

export const deletedLessonDetails = (payload) => ({
  type: types.DELETED_LESSON_DETAILS,
  payload,
});

export const deleteLessonDetailsError = (payload) => ({
  type: types.DELETE_LESSON_DETAILS_ERROR,
  payload,
});

export const resetDeleteLessonDetailsFlag = () => ({
  type: types.RESET_DELETE_LESSON_DETAILS_FLAG,
});

// Save lesson sequence
export const saveLessonSequenceDetails = (courseId, lessons = []) => ({
  type: types.SAVE_LESSON_SEQUENCE_DETAILS,
  payload: {
    courseId,
    lessons,
  },
});

export const savingLessonSequenceDetails = () => ({
  type: types.SAVING_LESSON_SEQUENCE_DETAILS,
});

export const savedLessonSequenceDetails = (payload) => ({
  type: types.SAVED_LESSON_SEQUENCE_DETAILS,
  payload,
});

export const saveLessonSequenceDetailsError = (payload) => ({
  type: types.SAVE_LESSON_SEQUENCE_DETAILS_ERROR,
  payload,
});

export const resetSaveLessonSequenceDetailsFlag = () => ({
  type: types.RESET_SAVE_LESSON_SEQUENCE_DETAILS_FLAG,
});
