import React from "react";
import PropTypes from "prop-types";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "./breadcrumb.scss";
import { Link } from "react-router-dom";

function Breadcrumb(props) {
  const getItem = (item) => (
    <>
      {item.icon && (
        <FontAwesomeIcon icon={item.icon} className="text-dark mr-2" />
      )}
      <span className="font-weight-bold h6 text-dark">{item.label}</span>
    </>
  );

  return (
    <div id="cfg_breadcrumb" className="p-2 pl-3 d-flex">
      {props.scheme.map((item, index) => (
        <div key={`breadcrum-item-${index}`}>
          {item.to ? <Link to={item.to}>{getItem(item)}</Link> : getItem(item)}
          {index !== props.scheme.length - 1 && (
            <span className="mr-1 ml-1">/</span>
          )}
        </div>
      ))}
    </div>
  );
}

Breadcrumb.propTypes = {
  scheme: PropTypes.array.isRequired,
};

export default Breadcrumb;
