import * as types from "./types";
import * as bitsActions from "./actions";
import { createLogic } from "redux-logic";
import get from "lodash.get";

import axios from "axios";
import env from "app/environment";

const loadBitsLogic = createLogic({
  type: types.LOAD_BITS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(bitsActions.loadingBits());
    axios
      .get(env.BITS)
      .then((res) => {
        if (res.data.error) {
          dispatch(
            bitsActions.loadBitsError(
              res.data.message || "ERR: An error occured"
            )
          );
        } else {
          dispatch(bitsActions.setBits(get(res, `data.data.bits`, [])));
        }
      })
      .catch((err) => {
        dispatch(bitsActions.loadBitsError("Server error"));
      })
      .finally(() => {
        done();
      });
  },
});

export default [loadBitsLogic];
