import React, { useEffect, useState, useRef } from "react";
import BitDetailsComponent from "./BitDetailsComponent";
import { useDispatch, useSelector } from "react-redux";
import { bitOperations } from "app/store/state/active-bit";

function BitDetails(props) {
  const [editMode, setEditMode] = useState(false);
  const [deleteMode, setDeleteMode] = useState(false);
  const [isModified, setModifiedFlag] = useState(false);
  const [localBitData, setLocalBitData] = useState({});
  const activeBit = useSelector((state) => state.activeBit);
  const tinyMceApiKey = useSelector((state) => state.app.tinyMceApiKey);
  const dispatch = useDispatch();
  const activeBitData = activeBit.data;
  const prevActiveBitDeletedSuccessRef = useRef();

  useEffect(() => {
    prevActiveBitDeletedSuccessRef.current = activeBit.deletedSuccess;
  });

  useEffect(() => {
    if (activeBitData) {
      setModifiedFlag(false);
      setLocalBitData(activeBitData);
    }
  }, [activeBitData]);

  useEffect(() => {
    const bitId = props.match.params.id;
    dispatch(bitOperations.loadBit(bitId));
    // eslint-disable-next-line
  }, [props.match.params.id]);

  const toggleEditMode = () => {
    setEditMode(!editMode);
  };

  const toggleDeleteMode = () => {
    setDeleteMode(!deleteMode);
  };

  const handleChange = (field, e) => {
    e.stopPropagation();
    if (field) {
      const newValue = e.target.value;
      const oldValue = localBitData[field];
      if (newValue !== oldValue) {
        setLocalBitData({
          ...localBitData,
          [field]: newValue,
        });
        if (!isModified) setModifiedFlag(true);
      }
    }
  };

  const deleteBit = (e) => {
    e.stopPropagation();
    toggleDeleteMode();
    dispatch(bitOperations.deleteBitDetails());
  };

  const saveBitDetails = (e) => {
    e.preventDefault();
    dispatch(bitOperations.saveBitDetails(localBitData));
  };

  const handleEditorChange = (field, overview) => {
    setLocalBitData({
      ...localBitData,
      [field]: overview,
    });
    if (!isModified) setModifiedFlag(true);
  };

  const handleTagsChange = (field, tags) => {
    setLocalBitData({
      ...localBitData,
      [field]: tags,
    });
    if (!isModified) setModifiedFlag(true);
  };

  return (
    <BitDetailsComponent
      activeBit={activeBit}
      localBitData={localBitData}
      editModeEnabled={editMode}
      isModified={isModified}
      deleteModeEnabled={deleteMode}
      prevActiveBitDeletedSuccessRef={prevActiveBitDeletedSuccessRef}
      tinyMceApiKey={tinyMceApiKey}
      handleEditorChange={handleEditorChange}
      handleChange={handleChange}
      toggleDeleteMode={toggleDeleteMode}
      toggleEditMode={toggleEditMode}
      saveBitDetails={saveBitDetails}
      deleteBit={deleteBit}
      handleTagsChange={handleTagsChange}
    />
  );
}

export default BitDetails;
