import axios from "axios";
import env from "app/environment";

export const setAuthToken = (token) => {
  if (token) {
    // Apply authorization token to every request if logged in
    axios.defaults.headers.common["x-access-token"] = token;
    axios.defaults.headers.common["Content-Type"] = "application/json";
  } else {
    // Delete auth header
    delete axios.defaults.headers.common["x-access-token"];
  }
};

export const uploadImageFromFile = (file, success, failure) => {
  const blob = new Blob([file]);
  uploadImage(file.name, blob, success, failure);
};

export const uploadImageFromBlobInfo = (blobInfo, success, failure) => {
  const blob = blobInfo.blob();
  let fileName;

  if (typeof blob.name !== undefined) fileName = blob.name;
  else fileName = blobInfo.filename();

  uploadImage(fileName, blob, success, failure);
};

const uploadImage = (fileName, blob, success, failure) => {
  const file = new FormData();

  file.append("photo", blob, fileName);

  axios
    .post(env.IMAGE, file, {
      "Content-Type": `multipart/form-data; boundary=${file._boundary}`,
    })
    .then((res) => {
      if (res.data.error) {
        failure(res.data.message || "An error occured");
      } else {
        success(res.data.path);
      }
    })
    .catch((err) => {
      failure("Failed: An error occured");
    });
};
