import * as types from "./types";
import * as courseLessonActions from "./actions";
import { createLogic } from "redux-logic";
import get from "lodash.get";
import axios from "axios";
import env from "app/environment";
import {
  SAVE_DETAILS_FLAG_RESET_DELAY,
  DELETE_DETAILS_FLAG_RESET_DELAY,
} from "app/constants/delay";

const loadLessonsLogic = createLogic({
  type: types.LOAD_LESSONS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseLessonActions.loadingLessons());
    const courseId = action.payload;
    if (courseId) {
      axios
        .get(env.LESSONS(courseId))
        .then((res) => {
          if (res.data.error) {
            dispatch(
              courseLessonActions.loadLessonsError(
                res.data.message || "ERR: An error occured"
              )
            );
          } else {
            dispatch(courseLessonActions.setLessons(get(res, `data.data`, [])));
          }
        })
        .catch((err) => {
          dispatch(courseLessonActions.loadLessonsError("Server error"));
        })
        .finally(() => {
          done();
        });
    } else {
      done();
    }
  },
});

const saveLessonDetailsLogic = createLogic({
  type: types.SAVE_LESSON_DETAILS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseLessonActions.savingLessonDetails());
    const { data: lesson, mode, courseId: activeCourseId } = action.payload;

    if (activeCourseId) {
      let operation = undefined;
      let url = undefined;

      if (mode === "add") {
        operation = axios.post;
        url = env.LESSONS(activeCourseId);
      }
      if (mode === "update") {
        const { id: lessonId } = lesson;
        operation = axios.put;
        url = env.LESSON(activeCourseId, lessonId);
      }

      if (operation && url) {
        operation(url, lesson)
          .then((res) => {
            if (res.data.error) {
              const message = get(
                res,
                `data.message`,
                `An error occured while ${
                  mode === "add" ? "adding new lesson" : "updating the lesson"
                }`
              );
              dispatch(courseLessonActions.saveLessonDetailsError(message));
              setTimeout(() => {
                dispatch(courseLessonActions.resetSaveLessonDetailsFlag());
                done();
              }, SAVE_DETAILS_FLAG_RESET_DELAY);
            } else {
              const message = get(
                res,
                `data.data.message`,
                mode === "add" ? "Added successfully" : "Updated successfully"
              );

              dispatch(courseLessonActions.savedLessonDetails(message));
              dispatch(courseLessonActions.loadLessons(activeCourseId));

              setTimeout(() => {
                dispatch(courseLessonActions.resetSaveLessonDetailsFlag());
                done();
              }, SAVE_DETAILS_FLAG_RESET_DELAY);
            }
          })
          .catch((err) => {
            dispatch(
              courseLessonActions.saveLessonDetailsError("Server error")
            );
            setTimeout(() => {
              dispatch(courseLessonActions.resetSaveLessonDetailsFlag());
              done();
            }, SAVE_DETAILS_FLAG_RESET_DELAY);
          });
      } else done();
    } else {
      done();
    }
  },
});

const deleteLessonDetailsLogic = createLogic({
  type: types.DELETE_LESSON_DETAILS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseLessonActions.deletingLessonDetails());
    const { courseId, lessonId } = action.payload;

    if (courseId && lessonId) {
      axios
        .delete(env.LESSON(courseId, lessonId))
        .then((res) => {
          if (res.data.error) {
            const message = get(
              res,
              `data.message`,
              "An error occured while deleting"
            );
            dispatch(courseLessonActions.deleteLessonDetailsError(message));
            setTimeout(() => {
              dispatch(courseLessonActions.resetDeleteLessonDetailsFlag());
              done();
            }, DELETE_DETAILS_FLAG_RESET_DELAY);
          } else {
            const message = get(
              res,
              `data.data.message`,
              "Deleted successfully"
            );

            dispatch(courseLessonActions.deletedLessonDetails(message));
            dispatch(courseLessonActions.loadLessons(courseId));

            setTimeout(() => {
              dispatch(courseLessonActions.resetDeleteLessonDetailsFlag());
              done();
            }, DELETE_DETAILS_FLAG_RESET_DELAY);
          }
        })
        .catch((err) => {
          dispatch(
            courseLessonActions.deleteLessonDetailsError("Server error")
          );
          setTimeout(() => {
            dispatch(courseLessonActions.resetDeleteLessonDetailsFlag());
            done();
          }, DELETE_DETAILS_FLAG_RESET_DELAY);
        });
    } else {
      done();
    }
  },
});

const saveLessonSequenceDetailsLogic = createLogic({
  type: types.SAVE_LESSON_SEQUENCE_DETAILS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseLessonActions.savingLessonSequenceDetails());
    const { lessons, courseId } = action.payload;

    if (lessons.length !== 0) {
      const promiseChain = lessons.map((lesson, index) => {
        return new Promise((resolve, reject) => {
          axios
            .put(env.LESSON(courseId, lesson.id), {
              ...lesson,
              lessonOrder: ++index,
            })
            .then((res) => {
              if (res.data.error) {
                reject();
              } else {
                resolve();
              }
            })
            .catch((err) => {
              reject();
            });
        });
      });

      Promise.all(promiseChain)
        .then((val) => {
          dispatch(
            courseLessonActions.savedLessonSequenceDetails(
              "Sequence updated successfully!"
            )
          );
          dispatch(courseLessonActions.loadLessons(courseId));

          setTimeout(() => {
            dispatch(courseLessonActions.resetSaveLessonSequenceDetailsFlag());
            done();
          }, SAVE_DETAILS_FLAG_RESET_DELAY);
        })
        .catch((err) => {
          dispatch(
            courseLessonActions.saveLessonSequenceDetailsError(
              "Sequence update failed!"
            )
          );
          setTimeout(() => {
            dispatch(courseLessonActions.resetSaveLessonSequenceDetailsFlag());
            done();
          }, SAVE_DETAILS_FLAG_RESET_DELAY);
        });
    } else {
      done();
    }
  },
});

export default [
  loadLessonsLogic,
  saveLessonDetailsLogic,
  deleteLessonDetailsLogic,
  saveLessonSequenceDetailsLogic,
];
