import * as types from "./types";
import * as courseActions from "./actions";
import { createLogic } from "redux-logic";
import get from "lodash.get";

import axios from "axios";
import env from "app/environment";
import {
  SAVE_DETAILS_FLAG_RESET_DELAY,
  DELETE_DETAILS_FLAG_RESET_DELAY,
} from "app/constants/delay";

const loadCourseLogic = createLogic({
  type: types.LOAD_COURSE,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseActions.loadingCourse());
    const courseId = action.payload;
    if (courseId) {
      axios
        .get(env.COURSE(courseId))
        .then((res) => {
          if (res.data.error) {
            dispatch(
              courseActions.loadCourseError(
                res.data.message || "ERR: An error occured"
              )
            );
          } else {
            dispatch(courseActions.setCourse(get(res, `data.data.course`, [])));
          }
        })
        .catch((err) => {
          dispatch(courseActions.loadCourseError("Server error"));
        })
        .finally(() => {
          done();
        });
    } else {
      done();
    }
  },
});

const saveCourseDetailsLogic = createLogic({
  type: types.SAVE_COURSE_DETAILS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseActions.savingCourseDetails());
    const { data: courseDetails, mode } = action.payload;

    if (mode === "update") {
      const activeCourseId = get(getState(), `activeCourse.data.id`, undefined);
      if (activeCourseId) {
        axios
          .put(env.COURSE(activeCourseId), courseDetails)
          .then((res) => {
            if (res.data.error) {
              const message = get(
                res,
                `data.message`,
                "An error occured while updating"
              );
              dispatch(courseActions.saveCourseDetailsError(message));
              setTimeout(() => {
                dispatch(courseActions.resetSaveCourseDetailsFlag());
                done();
              }, SAVE_DETAILS_FLAG_RESET_DELAY);
            } else {
              const message = get(
                res,
                `data.data.message`,
                "Updated successfully"
              );

              dispatch(courseActions.savedCourseDetails(message));
              dispatch(courseActions.loadCourse(activeCourseId));

              setTimeout(() => {
                dispatch(courseActions.resetSaveCourseDetailsFlag());
                done();
              }, SAVE_DETAILS_FLAG_RESET_DELAY);
            }
          })
          .catch((err) => {
            dispatch(courseActions.saveCourseDetailsError("Server error"));
            setTimeout(() => {
              dispatch(courseActions.resetSaveCourseDetailsFlag());
              done();
            }, SAVE_DETAILS_FLAG_RESET_DELAY);
          });
      } else {
        console.log("Active course Id not found");
        done();
      }
    } else if (mode === "add") {
      axios
        .post(env.COURSES, courseDetails)
        .then((res) => {
          if (res.data.error) {
            const message = get(
              res,
              `data.message`,
              "An error occured while updating"
            );
            dispatch(courseActions.saveCourseDetailsError(message));
            setTimeout(() => {
              dispatch(courseActions.resetSaveCourseDetailsFlag());
              done();
            }, SAVE_DETAILS_FLAG_RESET_DELAY);
          } else {
            const message = get(
              res,
              `data.data.message`,
              "Updated successfully"
            );

            dispatch(courseActions.savedCourseDetails(message));

            setTimeout(() => {
              dispatch(courseActions.resetSaveCourseDetailsFlag());
              done();
            }, SAVE_DETAILS_FLAG_RESET_DELAY);
          }
        })
        .catch((err) => {
          dispatch(courseActions.saveCourseDetailsError("Server error"));
          setTimeout(() => {
            dispatch(courseActions.resetSaveCourseDetailsFlag());
            done();
          }, SAVE_DETAILS_FLAG_RESET_DELAY);
        });
    } else {
      done();
    }
  },
});

const deleteCourseDetailsLogic = createLogic({
  type: types.DELETE_COURSE_DETAILS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseActions.deletingCourseDetails());
    const activeCourseId = get(getState(), `activeCourse.data.id`, undefined);

    if (activeCourseId) {
      axios
        .delete(env.COURSE(activeCourseId))
        .then((res) => {
          if (res.data.error) {
            const message = get(
              res,
              `data.message`,
              "An error occured while deleting"
            );
            dispatch(courseActions.deleteCourseDetailsError(message));
            setTimeout(() => {
              dispatch(courseActions.resetDeleteCourseDetailsFlag());
              done();
            }, DELETE_DETAILS_FLAG_RESET_DELAY);
          } else {
            const message = get(
              res,
              `data.data.message`,
              "Deleted successfully"
            );

            dispatch(courseActions.deletedCourseDetails(message));
            // dispatch(courseActions.loadCourse(activeCourseId));

            setTimeout(() => {
              dispatch(courseActions.resetDeleteCourseDetailsFlag());
              done();
            }, DELETE_DETAILS_FLAG_RESET_DELAY);
          }
        })
        .catch((err) => {
          dispatch(courseActions.deleteCourseDetailsError("Server error"));
          setTimeout(() => {
            dispatch(courseActions.resetDeleteCourseDetailsFlag());
            done();
          }, DELETE_DETAILS_FLAG_RESET_DELAY);
        });
    } else {
      done();
    }
  },
});

const publishCourseDetailsLogic = createLogic({
  type: types.PUBLISH_COURSE_DETAILS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseActions.publishingCourseDetails());
    const { id, isPublished } = action.payload;
    if (id) {
      axios
        .post(env.PUBLISH, {
          id,
          publish: isPublished,
        })
        .then((res) => {
          if (res.data.error) {
            const message = get(
              res,
              `data.message`,
              "An error occured while publishing"
            );
            dispatch(courseActions.publishCourseDetailsError(message));
            setTimeout(() => {
              dispatch(courseActions.resetPublishCourseDetailsFlag());
              done();
            }, SAVE_DETAILS_FLAG_RESET_DELAY);
          } else {
            const message = get(
              res,
              `data.data.message`,
              "Published successfully"
            );

            dispatch(courseActions.publishedCourseDetails(message));
            dispatch(courseActions.loadCourse(id));

            setTimeout(() => {
              dispatch(courseActions.resetPublishCourseDetailsFlag());
              done();
            }, SAVE_DETAILS_FLAG_RESET_DELAY);
          }
        })
        .catch((err) => {
          dispatch(courseActions.publishCourseDetailsError("Server error"));
          setTimeout(() => {
            dispatch(courseActions.resetPublishCourseDetailsFlag());
            done();
          }, SAVE_DETAILS_FLAG_RESET_DELAY);
        });
    } else {
      done();
    }
  },
});

export default [
  loadCourseLogic,
  saveCourseDetailsLogic,
  deleteCourseDetailsLogic,
  publishCourseDetailsLogic,
];
