import produce from "immer";
import * as types from "./types";

const initialState = {
  // Loading
  isLoading: false,
  error: false,
  errMsg: undefined,
  data: [],

  // Saving
  isSaving: false,
  savedSuccess: false,
  saveError: false,
  saveMsg: undefined,

  // Deleting
  isDeleting: false,
  deletedSuccess: false,
  deleteError: false,
  deleteMsg: undefined,

  // Sequence
  isSequenceSaving: false,
  sequenceSaveError: false,
  sequenceSaveSuccess: false,
  sequenceSaveMsg: undefined,
};

const appReducer = (state = initialState, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case types.SAVING_LESSON_SEQUENCE_DETAILS: {
        draft.isSequenceSaving = true;
        draft.sequenceSaveError = false;
        draft.sequenceSaveMsg = undefined;
        draft.sequenceSaveSuccess = false;
        break;
      }
      case types.SAVED_LESSON_SEQUENCE_DETAILS: {
        const { payload } = action;
        draft.isSequenceSaving = false;
        draft.sequenceSaveError = false;
        draft.sequenceSaveMsg = payload;
        draft.sequenceSaveSuccess = true;
        break;
      }
      case types.SAVE_LESSON_SEQUENCE_DETAILS_ERROR: {
        const { payload } = action;
        draft.isSequenceSaving = false;
        draft.sequenceSaveError = true;
        draft.sequenceSaveMsg = payload;
        draft.sequenceSaveSuccess = false;
        break;
      }
      case types.RESET_SAVE_LESSON_SEQUENCE_DETAILS_FLAG: {
        if (state.sequenceSaveSuccess) draft.sequenceSaveSuccess = false;
        if (state.sequenceSaveError) draft.sequenceSaveError = false;
        draft.sequenceSaveMsg = undefined;
        break;
      }

      case types.LOADING_LESSONS: {
        draft.isLoading = true;
        draft.error = false;
        draft.errMsg = undefined;
        break;
      }
      case types.SET_LESSONS: {
        const { payload } = action;
        draft.isLoading = false;
        draft.data = payload;
        draft.error = false;
        draft.errMsg = undefined;
        break;
      }
      case types.LOAD_LESSONS_ERROR: {
        const { payload } = action;
        draft.isLoading = false;
        draft.error = true;
        draft.errMsg = payload;
        break;
      }
      //Saving Course Details
      case types.SAVING_LESSON_DETAILS: {
        draft.isSaving = true;
        draft.saveError = false;
        draft.saveMsg = undefined;
        draft.savedSuccess = false;
        break;
      }
      case types.SAVED_LESSON_DETAILS: {
        const { payload } = action;
        draft.isSaving = false;
        draft.saveError = false;
        draft.saveMsg = payload;
        draft.savedSuccess = true;
        break;
      }
      case types.SAVE_LESSON_DETAILS_ERROR: {
        const { payload } = action;
        draft.isSaving = false;
        draft.saveError = true;
        draft.saveMsg = payload;
        draft.savedSuccess = false;
        break;
      }
      case types.RESET_SAVE_LESSON_DETAILS_FLAG: {
        if (state.savedSuccess) draft.savedSuccess = false;
        if (state.saveError) draft.saveError = false;
        draft.saveMsg = undefined;
        break;
      }
      //Deleting Course Details
      case types.DELETING_LESSON_DETAILS: {
        draft.isDeleting = true;
        draft.deleteError = false;
        draft.deleteMsg = undefined;
        draft.deletedSuccess = false;
        break;
      }
      case types.DELETED_LESSON_DETAILS: {
        const { payload } = action;
        draft.isDeleting = false;
        draft.deleteError = false;
        draft.deleteMsg = payload;
        draft.deletedSuccess = true;
        break;
      }
      case types.DELETE_LESSON_DETAILS_ERROR: {
        const { payload } = action;
        draft.isDeleting = false;
        draft.deleteError = true;
        draft.deleteMsg = payload;
        draft.deletedSuccess = false;
        break;
      }
      case types.RESET_DELETE_LESSON_DETAILS_FLAG: {
        if (state.deletedSuccess) draft.deletedSuccess = false;
        if (state.deleteError) draft.deleteError = false;
        draft.deleteMsg = undefined;
        break;
      }
      default:
        break;
    }
  });

export default appReducer;
