import * as types from "./types";
import * as courseModuleActions from "./actions";
import { createLogic } from "redux-logic";
import get from "lodash.get";
import axios from "axios";
import env from "app/environment";
import {
  SAVE_DETAILS_FLAG_RESET_DELAY,
  DELETE_DETAILS_FLAG_RESET_DELAY,
} from "app/constants/delay";

const loadModulesLogic = createLogic({
  type: types.LOAD_MODULES,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseModuleActions.loadingModules());
    const courseId = action.payload;
    if (courseId) {
      axios
        .get(env.MODULES(courseId))
        .then((res) => {
          if (res.data.error) {
            dispatch(
              courseModuleActions.loadModulesError(
                res.data.message || "ERR: An error occured"
              )
            );
          } else {
            dispatch(courseModuleActions.setModules(get(res, `data.data`, [])));
          }
        })
        .catch((err) => {
          dispatch(courseModuleActions.loadModulesError("Server error"));
        })
        .finally(() => {
          done();
        });
    } else {
      done();
    }
  },
});

const saveModuleDetailsLogic = createLogic({
  type: types.SAVE_MODULE_DETAILS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseModuleActions.savingModuleDetails());
    const { data: Module, mode, courseId: activeCourseId } = action.payload;

    if (activeCourseId) {
      let operation = undefined;
      let url = undefined;

      if (mode === "add") {
        operation = axios.post;
        url = env.MODULES(activeCourseId);
      }
      if (mode === "update") {
        const { id: ModuleId } = Module;
        operation = axios.put;
        url = env.MODULE(activeCourseId, ModuleId);
      }

      if (operation && url) {
        operation(url, Module)
          .then((res) => {
            if (res.data.error) {
              const message = get(
                res,
                `data.message`,
                `An error occured while ${
                  mode === "add" ? "adding new Module" : "updating the Module"
                }`
              );
              dispatch(courseModuleActions.saveModuleDetailsError(message));
              setTimeout(() => {
                dispatch(courseModuleActions.resetSaveModuleDetailsFlag());
                done();
              }, SAVE_DETAILS_FLAG_RESET_DELAY);
            } else {
              const message = get(
                res,
                `data.data.message`,
                mode === "add" ? "Added successfully" : "Updated successfully"
              );

              dispatch(courseModuleActions.savedModuleDetails(message));
              dispatch(courseModuleActions.loadModules(activeCourseId));

              setTimeout(() => {
                dispatch(courseModuleActions.resetSaveModuleDetailsFlag());
                done();
              }, SAVE_DETAILS_FLAG_RESET_DELAY);
            }
          })
          .catch((err) => {
            dispatch(
              courseModuleActions.saveModuleDetailsError("Server error")
            );
            setTimeout(() => {
              dispatch(courseModuleActions.resetSaveModuleDetailsFlag());
              done();
            }, SAVE_DETAILS_FLAG_RESET_DELAY);
          });
      } else done();
    } else {
      done();
    }
  },
});

const deleteModuleDetailsLogic = createLogic({
  type: types.DELETE_MODULE_DETAILS,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(courseModuleActions.deletingModuleDetails());
    const { courseId, moduleId } = action.payload;

    if (courseId && moduleId) {
      axios
        .delete(env.MODULE(courseId, moduleId))
        .then((res) => {
          if (res.data.error) {
            const message = get(
              res,
              `data.message`,
              "An error occured while deleting"
            );
            dispatch(courseModuleActions.deleteModuleDetailsError(message));
            setTimeout(() => {
              dispatch(courseModuleActions.resetDeleteModuleDetailsFlag());
              done();
            }, DELETE_DETAILS_FLAG_RESET_DELAY);
          } else {
            const message = get(
              res,
              `data.data.message`,
              "Deleted successfully"
            );

            dispatch(courseModuleActions.deletedModuleDetails(message));
            dispatch(courseModuleActions.loadModules(courseId));

            setTimeout(() => {
              dispatch(courseModuleActions.resetDeleteModuleDetailsFlag());
              done();
            }, DELETE_DETAILS_FLAG_RESET_DELAY);
          }
        })
        .catch((err) => {
          dispatch(
            courseModuleActions.deleteModuleDetailsError("Server error")
          );
          setTimeout(() => {
            dispatch(courseModuleActions.resetDeleteModuleDetailsFlag());
            done();
          }, DELETE_DETAILS_FLAG_RESET_DELAY);
        });
    } else {
      done();
    }
  },
});

export default [
  loadModulesLogic,
  saveModuleDetailsLogic,
  deleteModuleDetailsLogic,
];
