import React from "react";
import PropTypes from "prop-types";
import Card from "react-bootstrap/Card";
import Badge from "react-bootstrap/Badge";
import Button from "react-bootstrap/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAngleRight,
  faPencilAlt,
  faTrash,
  faTags,
} from "@fortawesome/free-solid-svg-icons";
import "./bit-card.scss";

function BitCard({
  id,
  name,
  content,
  tags = [],
  openBitDetails,
  toggleEditMode,
  toggleDeleteMode,
  mode = "list",
}) {
  const openBit = (e) => {
    e.stopPropagation();
    openBitDetails(id);
  };
  return (
    <Card body className={`bit-card`}>
      <div className="bit-wrapper p-3">
        <div className="bit-image">
          <FontAwesomeIcon icon={faTags} size="2x" />
        </div>
        <div className="bit-info ml-2">
          <div
            className={`bit-title ${
              mode === "header" ? "h5" : "h6"
            } clamp-1 ellipsis font-weight-bold mb-0`}
          >
            {name}
          </div>
          {mode === "list" && (
            <small className="bit-overview text-muted ellipsis clamp-3">
              {content}
            </small>
          )}
          {mode === "list" && (
            <div className="bit-tags mt-1 clamp-1 ellipsis">
              {tags.length !== 0 ? (
                <div className="tags">
                  {tags.map((tag, index) => (
                    <Badge
                      pill
                      variant="primary"
                      className="mr-1"
                      key={`tag-${index}`}
                    >
                      {tag}
                    </Badge>
                  ))}
                </div>
              ) : null}
            </div>
          )}
        </div>

        <div className="action-panel">
          {mode === "list" && (
            <div className="action-button pointer" onClick={openBit}>
              <FontAwesomeIcon icon={faAngleRight} />
            </div>
          )}
          {mode === "header" && (
            <>
              <Button
                variant="warning"
                className="ml-2"
                onClick={toggleEditMode}
              >
                <FontAwesomeIcon icon={faPencilAlt} className="mr-2" />
                Edit
              </Button>
              <Button
                variant="danger"
                className="ml-2"
                onClick={toggleDeleteMode}
              >
                <FontAwesomeIcon icon={faTrash} className="mr-2" />
                Delete
              </Button>
            </>
          )}
        </div>
      </div>
    </Card>
  );
}

BitCard.propTypes = {
  id: PropTypes.string,
  name: PropTypes.string,
  slug: PropTypes.string,
  content: PropTypes.any,
  tags: PropTypes.any,
  author: PropTypes.string,
};

export default BitCard;
