import * as types from "./types";
import * as coursesActions from "./actions";
import { createLogic } from "redux-logic";
import get from "lodash.get";

import axios from "axios";
import env from "app/environment";

const loadCoursesLogic = createLogic({
  type: types.LOAD_COURSES,
  latest: true,
  process({ getState, action }, dispatch, done) {
    dispatch(coursesActions.loadingCourses());
    axios
      .get(env.COURSES)
      .then((res) => {
        if (res.data.error) {
          dispatch(
            coursesActions.loadCoursesError(
              res.data.message || "ERR: An error occured"
            )
          );
        } else {
          dispatch(
            coursesActions.setCourses(get(res, `data.data.courses`, []))
          );
        }
      })
      .catch((err) => {
        dispatch(coursesActions.loadCoursesError("Server error"));
      })
      .finally(() => {
        done();
      });
  },
});

export default [loadCoursesLogic];
