import * as types from "./types";

// Loading Module Data Actions
export const loadModules = (payload) => ({
  type: types.LOAD_MODULES,
  payload,
});

export const loadingModules = () => ({
  type: types.LOADING_MODULES,
});

export const setModules = (payload) => ({
  type: types.SET_MODULES,
  payload,
});

export const loadModulesError = (payload) => ({
  type: types.LOAD_MODULES_ERROR,
  payload,
});

// Saving Module Details Actions
export const addNewModuleDetails = (courseId, module) => ({
  type: types.SAVE_MODULE_DETAILS,
  payload: {
    mode: "add",
    courseId,
    data: module,
  },
});

export const saveModuleDetails = (courseId, module) => ({
  type: types.SAVE_MODULE_DETAILS,
  payload: {
    mode: "update",
    courseId,
    data: module,
  },
});

export const savingModuleDetails = () => ({
  type: types.SAVING_MODULE_DETAILS,
});

export const savedModuleDetails = (payload) => ({
  type: types.SAVED_MODULE_DETAILS,
  payload,
});

export const saveModuleDetailsError = (payload) => ({
  type: types.SAVE_MODULE_DETAILS_ERROR,
  payload,
});

export const resetSaveModuleDetailsFlag = () => ({
  type: types.RESET_SAVE_MODULE_DETAILS_FLAG,
});

// Deleting Module Details Actions
export const deleteModuleDetails = (courseId, moduleId) => ({
  type: types.DELETE_MODULE_DETAILS,
  payload: {
    courseId,
    moduleId,
  },
});

export const deletingModuleDetails = () => ({
  type: types.DELETING_MODULE_DETAILS,
});

export const deletedModuleDetails = (payload) => ({
  type: types.DELETED_MODULE_DETAILS,
  payload,
});

export const deleteModuleDetailsError = (payload) => ({
  type: types.DELETE_MODULE_DETAILS_ERROR,
  payload,
});

export const resetDeleteModuleDetailsFlag = () => ({
  type: types.RESET_DELETE_MODULE_DETAILS_FLAG,
});
